<template>
    <Page>
        <Header>
            <Navbar>
                <Title>Modal</Title>
            </Navbar>
        </Header>
        <Content padding>
            <h5></h5>

            <p>Modal, , //</p>


            <Button block @click="openModal">Modal</Button>

            <h5></h5>
            <p>Modal, ModalSlides</p>
            <Button block @click="openAlbum"></Button>
            <hr>
            <h5>:</h5>
            <p>, , modal.</p>
            <Button block @click="openLogin"></Button>
            <p><strong></strong></p>
            <p>: {{username}}</p>
            <p>: {{password}}</p>
        </Content>
    </Page>
</template>
<style lang="scss">
    .login {
        border: 1px solid #ddd;
        border-radius: 5px;
        padding: 30px 10px;
    }

</style>
<script type="text/javascript">
  import modalPageComponent from './modal-page.vue'
  import modalAlbumComponent from './modal-album.vue'
  import modalLoginComponent from './modal-login.vue'
  export default{
    data () {
      return {
        username: 'Vimo',
        password: '123456'
      }
    },
    props: {},
    watch: {},
    computed: {},
    methods: {
      openLogin () {
        const _this = this
        this.$modal.present({
          component: modalLoginComponent,
          data: {
            username: this.username,
            password: this.password
          },
          showBackdrop: true,
          enableBackdropDismiss: false,
          onDismiss (data) {
            console.log(data)
            if (data) {
              _this.username = data.username
              _this.password = data.password
            }
          }
        })
      },
      openAlbum () {
        this.$modal.present({
          component: modalAlbumComponent,
          mode: 'zoom',
          data: {
            img: [
              'static/background/1.jpg',
              'static/background/2.jpg',
              'static/background/3.jpg',
              'static/background/4.jpg'
            ]
          },
          showBackdrop: true,
          enableBackdropDismiss: true
        })
      },
      openModal () {
        this.$modal.present({
          component: modalPageComponent,
          onDismiss () {
            console.log('onDismiss')
          }
        })
      }
    },
    created () {},
    mounted () {},
    activated () {},
    components: {}
  }
</script>



